import { Component, h, State, Event, Prop, EventEmitter } from '@stencil/core';
import { IDocument } from '../../types/Document';

@Component({
    tag: 'create-email',
    shadow: true,
    styleUrl: 'create-email.scss',
})
export class CreateEmail {
    @Prop() public document: IDocument;

    @Event() setEmailSubject: EventEmitter;
    @Event() setEmailMessage: EventEmitter;

    @State() private emailSubject: string = '';
    @State() private emailMessage: string = '';

    componentWillLoad() {
        this.emailSubject = this.document.email_send_subject;
        this.emailMessage = this.document.email_send_message;
    }

    constructor() {
        this.handleChangeEmailSubject =
            this.handleChangeEmailSubject.bind(this);
        this.handleChangeEmailMessage =
            this.handleChangeEmailMessage.bind(this);
    }

    public render() {
        return [
            <div>
                <h3>Write your email</h3>
                <limel-input-field
                    label="Subject"
                    value={this.emailSubject}
                    onChange={this.handleChangeEmailSubject}
                />

                <span class="send-document-email-subject">Email</span>
                <textarea
                    class="send-document-email"
                    rows={9}
                    value={this.emailMessage}
                    onChange={this.handleChangeEmailMessage}
                />
            </div>,
        ];
    }

    private handleChangeEmailSubject(event) {
        this.emailSubject = event.detail;
        this.setEmailSubject.emit(event.detail);
    }
    private handleChangeEmailMessage(event) {
        this.setEmailMessage.emit(event.target.value);
    }
}
