@import '../../gacolors.scss';

.send-document-email {
    width: calc(100% - 1.2rem);
    border-color: rgba($ga-black, .12);
    font-size: .875rem;
    line-height: 1.125rem;
    font-weight: 400;
    padding: 0.5rem;

    &:focus {
        outline: none;
        border-color: $lime-green;
    }

}

.send-document-email-subject {
    margin-top: 1rem;
    display: block;
    margin-left: 1rem;
    font-size: 0.675rem;
}
